
                $minWidthPC: 769px;
$maxWidthMB: 768px;
                @mixin mobile { @media (max-width: $maxWidthMB) { @content; } }
                @mixin pc { @media (min-width: $minWidthPC) { @content; } }
              
#lan-cards {
  position: relative;
  height: 1080px;
  @include mobile {
    height: 1886px;
  }
  .background {
    position: absolute;
    top: 0;
    left: 0;
  }
  .content {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    box-sizing: border-box;
  }
  .content {
    .list-cards {
      position: absolute;
      left: 49.9%;
      transform: translateX(-50%);
      top: 0.5%;
      display: flex;
      @include mobile {
        left: 50.2%;
        top: -4.2%;
      }
      .card {
        text-decoration: none;
        position: relative;
        pointer-events: none;
        opacity: 1;
        .MJ__toogleActive {
          pointer-events: auto;
        }
        &.card-1 {
          order: 1;
          right: auto;
          left: 415px;
          top: 88px;
          transform: scale(0.8);
          @include mobile {
            left: 951px;
            top: 998px;
          }
        }
        &.card-2 {
          order: 2;
          right: auto;
          left: 185px;
          top: 88px;
          transform: scale(0.8);
          opacity: 0.65;
          transition: opacity 0.6s ease;
          @include mobile {
            left: 387px;
            top: 540px;
          }
        }
        &.card-3 {
          order: 3;
          right: auto;
          left: 0;
          top: 0;
          transform: scale(1);
          opacity: 1;
          z-index: 2;
          transition: opacity 0.6s ease;
          @include mobile {
            left: 0;
            top: 0;
          }
        }
        &.card-4 {
          order: 4;
          left: auto;
          right: 193px;
          top: 88px;
          transform: scale(0.8);
          opacity: 0.65;
          transition: opacity 0.6s ease;
          @include mobile {
            right: 392px;
            top: 540px;
          }
        }
        &.card-5 {
          order: 5;
          left: auto;
          right: 422px;
          top: 88px;
          transform: scale(0.8);
          @include mobile {
            right: 956px;
            top: 998px;
          }
        }
        img {
          transform: scale(0.55);
          transform-origin: bottom;
          transition: transform 0.6s ease;
        }
        .tag {
          display: flex;
          align-items: center;
          justify-content: center;
          text-transform: uppercase;
          color: #efe4d0;
          font-size: 23px;
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          bottom: -22px;
        }
      }
    }
  }
}
