
                $minWidthPC: 769px;
$maxWidthMB: 768px;
                @mixin mobile { @media (max-width: $maxWidthMB) { @content; } }
                @mixin pc { @media (min-width: $minWidthPC) { @content; } }
              
#lan-story {
  position: relative;
  height: 1080px;
  @include mobile {
    height: 1688px;
  }
  .background {
    position: absolute;
    top: 0;
    left: 0;
  }

  .content {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    box-sizing: border-box;
  }
  .content {
    .image {
      position: absolute;
      left: 10.8%;
      top: 11.4%;
      @include mobile {
        transform: scale(0.85);
        left: 13.1%;
        top: 13%;
      }
    }
    .effect {
      position: absolute;
      top: 78.5%;
      left: 18.5%;
      z-index: 1;
      @include mobile {
        top: 52.5%;
        left: 23.5%;
      }
    }
    .back {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: -10px;
      filter: hue-rotate(128deg);
    }
    .text {
      font-size: 32px;
      color: #6d5128;
      position: absolute;
      left: 50%;
      top: 41%;
      width: 37%;
      line-height: 48px;
      @include mobile {
        left: 13%;
        top: 65%;
        width: 74%;
      }
    }
    .full-text {
      position: absolute;
      left: 45.4%;
      top: 11%;
      z-index: 1;
      display: none;
      @include mobile {
        left: 50%;
        transform: translateX(-50%);
        top: 16%;
      }
      &.active {
        display: block;
      }
      p {
        position: absolute;
        color: #6d5128;
        font-size: 27px;
        box-sizing: border-box;
        line-height: 42px;
        overflow-y: auto;
        height: 680px;
        width: 800px;
        padding-right: 22px;
        left: 6%;
        top: 6%;
        @include mobile {
          width: 89%;
          top: 4%;
          height: 100%;
          padding: 0;
          font-size: 26px;
          line-height: 42px;
          pointer-events: none;
        }
      }
    }
    .btn {
      position: absolute;
      left: 76%;
      top: 76%;
      @include mobile {
        left: 50%;
        transform: translateX(-50%);
        top: 91%;
      }
    }
  }
}
