.popup_register,
.popup_register2,
.popup_register3,
.popup_register4,
.popup_register5 {
  .content {
    .title {
      transform: scale(0.8);
      margin-top: 0px !important;
    }
    .description {
      position: absolute;
      left: 9%;
      top: 34%;
      font-size: 22px;
      color: #6d5128;
      width: 47%;
      text-align: left;
      line-height: 28px;
      @include mobile {
        width: 75%;
        font-size: 18px;
        line-height: 25px;
        top: 56%;
        left: 14%;
      }
    }
    .image {
      position: absolute;
      left: 58.3%;
      top: 2.5%;
      transform: scale(0.75);
      @include mobile {
        left: 50%;
        transform: translateX(-50%) scale(0.35);
        top: -12%;
      }
    }
    .effect {
      position: absolute;
      left: 63%;
      top: 79%;
      @include mobile {
        display: none;
      }
    }
    .text-custom {
      position: absolute;
      background-color: rgba(0, 0, 0, 0.8);
      left: 63.5%;
      bottom: 16.5%;
      width: 26.7%;
      @include mobile {
        left: 37.6%;
        bottom: 50.8%;
        width: 24.8%;
      }
      .image-text {
        display: flex;
        align-items: flex-end;
        color: #efe4d0;
        font-size: 30px;
        text-transform: uppercase;
        height: 52px;
        @include mobile {
          font-size: 21px;
          align-items: center;
          justify-content: center;
          height: auto;
          padding-top: 12px;
        }
      }
      ul {
        text-align: left;
        position: relative;
        left: 30px;
        color: #f7f3d3;
        font-size: 20px;
        margin: 8px 0 20px;
        @include mobile {
          font-size: 14px;
          width: 80%;
          margin: 2px 0 8px;
        }
        .vip {
          color: #f9d388;
        }
      }
    }
  }
}
