@import "./sprite.generated";

@import "./module/register.scss";

.base {
  .box {
    top: 50px;
    @include mobile {
      top: 180px;
    }
    .btn-close {
      @include sprite($btn-close);
      right: -38px;
      top: 0px;
      @include mobile {
        right: 50%;
        transform: translateX(50%);
                top: -50px;
      }
    }
    .content {
      padding: 50px 97px 140px 85px;
      display: flex;
      justify-content: flex-start;
      flex-direction: column;
      align-items: center;
      text-align: center;
      color: #6d5128;
      font-size: 24px;
      .btn {
        @include sprite($btn);
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        font-size: 30px;
        text-decoration: none;
        margin: 0 auto;
        text-transform: uppercase;
        font-weight: bold;
      }
      .title {
        font-size: 45px;
        display: flex;
        align-items: center;
        color: #fffca1;
        text-transform: uppercase;
        margin-top: 42px;
      }
    }
  }
  &.base-inform {
    .box {
      top: 250px;
      .btn-close {
        right: -50px;
        top: -20px;
      }
    }
  }
  &.base-big {
    .box {
      top: 220px;
      .content {
        color: #fef0bd;
        .text {
          margin-top: 28px;
          text-transform: uppercase;
          font-weight: bold;
          font-size: 22px;
        }
        .list-items {
          display: flex;
          margin-top: 42px;
          flex-wrap: wrap;
          font-size: 12px;
          width: 97.5%;
          justify-content: flex-start;
          column-gap: 6px;
          row-gap: 24px;
          height: 76%;
          overflow-y: auto;
          .item {
            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
              transform: scale(0.8);
            }
          }
        }
      }
    }
  }
  .template_history {
    .table_history {
      width: 95%;
      height: 100%;
      margin: 0 auto;

      @include mobile {
        width: 100%;
        height: 530px;
      }
      &::-webkit-scrollbar-thumb {
        background: #faa987;
      }
      table {
        // border: 4px solid #473021;
        // background-color: #fff4df;
        padding: 8px;
        border-collapse: collapse;
        width: 100%;
        thead {
          // background-color: #faa987;
          color: #662b04;

          tr {
            th {
              padding: 10px;

              // border: 1px solid #fbc2bd;
            }
          }
        }
        tbody {
          tr {
            &:first-child {
              border-top: 1px solid #f3fffc;
            }
            &:nth-child(even) {
              // background-color: #f4d3bd;
            }
            td {
              // border-top: 1px solid #f3fffc;
              padding: 10px;
              // border: 1px solid #fbc2bd;
              color: #662b04;
            }
          }
        }
      }
    }
  }
}
