
                $minWidthPC: 769px;
$maxWidthMB: 768px;
                @mixin mobile { @media (max-width: $maxWidthMB) { @content; } }
                @mixin pc { @media (min-width: $minWidthPC) { @content; } }
              
#lan-game {
  position: relative;
  height: 1080px;
  @include mobile {
    height: 1353px;
  }
  .background {
    position: absolute;
    top: 0;
    left: 0;
  }
  .content {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    box-sizing: border-box;
  }
  .content {
    .tab-1 {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      display: flex;
      flex-direction: column;
      align-items: center;
      top: 32%;
      gap: 10px;
      @include mobile {
        top: 21%;
      }
    }
    .tab-2 {
      position: absolute;
      left: 53%;
      transform: translateX(-50%);
      top: 29%;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 22px;
      @include mobile {
        top: 22%;
        left: 50%;
      }
      .text {
        color: #6d5128;
        font-size: 22px;
        position: absolute;
        top: 22%;
        left: 5%;
        width: 50%;
        line-height: 35px;
        @include mobile {
          width: 88%;
          left: 6%;
          top: 44%;
          font-size: 26px;
          line-height: 39px;
        }
      }
    }
    .tab-3 {
      position: absolute;
      left: 53%;
      transform: translateX(-50%);
      top: 33%;
      @include mobile {
        left: 50%;

        top: 21%;
      }
      .answer {
        position: absolute;
        top: 0;
        width: 100%;
        box-sizing: border-box;
        padding-left: 50px;
        padding-top: 80px;
        line-height: 40px;
        @include mobile {
          height: 100%;
          padding: 0;
          padding-top: 103%;
        }
        input {
          width: 49%;
          height: 52px;
          position: absolute;
          top: 218px;
          border: 1px solid #6d5128;
          box-sizing: border-box;
          padding-left: 12px;
          font-size: 21px;
          @include mobile {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            top: 75%;
            width: 83%;
          }
        }
        img {
          position: absolute;
          right: 3.8%;
          top: 52px;
          @include mobile {
            right: 10%;
            top: 30px;
          }
        }
        .text {
          color: #6d5128;
          font-size: 25px;
          width: 53%;
          height: 200px;
          margin-bottom: 10px;
          @include mobile {
            width: 100%;
            position: absolute;
            top: 55%;
            text-align: center;
            font-size: 28px;
            box-sizing: border-box;
            padding: 0 40px;
          }
        }
        .btn {
          text-decoration: none;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 27px;
          color: #efe4d0;

          @include mobile {
            margin-left: 20px;
            transform: scale(0.97);
          }
          &.submit {
            @include mobile {
              margin-top: 60px;
            }
          }
        }
      }
    }
  }
}
.loader {
  width: 48px;
  height: 48px;
  border: 5px solid;
  border-color: #ff3d00 transparent;
  border-radius: 50%;

  box-sizing: border-box;

  position: absolute;
  top: 81.9%;
  left: 23.4%;
  scale: 1.5;
  display: none;
  z-index: 2;
  @include mobile {
    top: 84.3%;
    left: 46.6%;
  }
  &.active {
    display: inline-block;
    animation: rotation 1s linear infinite;
  }
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
