
                $minWidthPC: 769px;
$maxWidthMB: 768px;
                @mixin mobile { @media (max-width: $maxWidthMB) { @content; } }
                @mixin pc { @media (min-width: $minWidthPC) { @content; } }
              
html,
body {
  overscroll-behavior-y: none; /* chặn scroll vượt top/bottom */
  overscroll-behavior-x: none;
  touch-action: manipulation;
  
}
