section {
  -webkit-overflow-scrolling: touch; /* vẫn mượt */
  overscroll-behavior: contain; /* chặn bounce vượt ra ngoài taskbar */
}

@font-face {
  font-family: "GS3";
  src: url("./fonts/GS3.TTF");
}


.MS__GS3 {
  font-family: "GS3";
}





.layer-rotate {
  width: 100%;
  height: 100%;
  background-color: #000;
  position: fixed;
  z-index: 100;
  opacity: 1;
  transition: opacity 0.5s linear;
  img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

.MS__outer.bottom {
  height: auto !important;
  transform-origin: left bottom !important;
  z-index: 1;
  .footer {
    position: absolute;
    bottom: 0;
    align-items: center;
    box-sizing: border-box;
    padding-bottom: 25px;
    padding-left: 150px;
    gap: 32px;
    font-size: 21px;
    line-height: 28px;
    color: #beccc9;
    display: none;
  }
  .btn {
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 20px;
    text-decoration: none;
    text-transform: uppercase;
    box-sizing: border-box;
    padding-bottom: 2px;
    gap: 8px;
    position: absolute;
    right: 0;
    pointer-events: auto;
    bottom: 30px;
  }
}
.MS__outer.popup {
  width: 10000px !important;

  z-index: 10;
}
.background-full {
  position: absolute;
  top: 0;
  left: 0;
}
.la {
  position: absolute;
  top: 0;
  left: 0;
  transform-origin: left top;
}
.bg-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  display: none;
  z-index: 1;
}
