
                $minWidthPC: 769px;
$maxWidthMB: 768px;
                @mixin mobile { @media (max-width: $maxWidthMB) { @content; } }
                @mixin pc { @media (min-width: $minWidthPC) { @content; } }
              
#lan-header {
  position: relative;
  height: 1080px;
  @include mobile {
    height: 1310px;
  }
  &.active {
    .list-btns {
      display: none !important;
    }
    .blur {
      display: block !important;
    }
    .background {
      .normal {
        display: none;
      }
      .active {
        display: block;
      }
    }
  }
  .background {
    position: absolute;
    top: 0;
    left: 0;
    .normal {
      display: block;
    }
    .active {
      display: none;
    }
  }
  video {
  }

  .content {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    box-sizing: border-box;
  }
  .content {
    .list-btns {
      display: flex;
      gap: 28px;
      position: absolute;
      left: 55%;
      top: 73%;
      @include mobile {
        left: 50%;
        top: 84%;
        transform: translateX(-50%) scale(0.9);
      }
    }
    .blur {
      display: none;
      position: absolute;
      color: #c3c1c1;
      font-size: 22px;
      top: 42%;
      left: 51%;
      width: 40%;
      @include mobile {
        left: 13%;
        top: 56%;
        width: 71%;
        line-height: 35px;
      }
      span {
        font-weight: bold;
        color: white;
      }
    }
  }
}
